@charset "UTF-8";
:root {
  --clip: left center;
  --size: 0;
  --background-thumb: #2b3769;
  --scrollbar-thumb-bgcolor: #757575;
  --shadow-size: 0px 0px 10px 1px rgba(0, 0, 0, 0.4);
  --stripe-width: 10px;
  --nav-marker-border: #3b8686;
  --sidebar-width: 28.5rem;
  --duration: 0.3s;
  --button-size: 4rem;
  --mob-expanded-width: 320px;
  --mob-collapsed-width: 4rem;
}

.masked-input {
  color: transparent; /* Скрыть текст, пока маска не активна */
}

.masked-input::placeholder {
  color: black; /* Цвет плейсхолдера */
}

.gradient-preloader {
  background: linear-gradient(90deg, #2b3769 -23.739%, #93a9ff 96.778%);
}

@media (min-width: 1440.99px) {
  .underline:hover {
    font-weight: 600;
    opacity: 1;
  }
  .underline:hover::before {
    width: 47%;
  }
  .underline:hover::after {
    width: 47%;
  }
  .underline::before {
    content: "";
    position: absolute;
    right: 0;
    bottom: 0px;
    width: 0;
    height: 2px;
    background-color: #000;
    opacity: 0.8;
    transition: width 0.4s ease-in-out;
  }
  .underline::after {
    content: "";
    position: absolute;
    left: 0;
    bottom: 0px;
    width: 0;
    height: 2px;
    background-color: #000;
    opacity: 0.8;
    transition: width 0.4s ease-in-out;
  }
  .underline-w:hover {
    font-weight: 600;
    opacity: 1;
  }
  .underline-w:hover::before {
    width: 47%;
  }
  .underline-w:hover::after {
    width: 47%;
  }
  .underline-w::before {
    content: "";
    position: absolute;
    right: 0;
    bottom: 0px;
    width: 0;
    height: 2px;
    background-color: #fff;
    opacity: 0.8;
    transition: width 0.4s ease-in-out;
  }
  .underline-w::after {
    content: "";
    position: absolute;
    left: 0;
    bottom: 0px;
    width: 0;
    height: 2px;
    background-color: #fff;
    opacity: 0.8;
    transition: width 0.4s ease-in-out;
  }
}
.seo-block {
  position: relative;
}
.seo-block__body {
  margin: 0 auto;
  display: flex;
  flex-direction: column;
}
.seo-block__title {
  margin-bottom: 49px;
  font-weight: 400;
}
@media screen and (max-width: 690px) {
  .seo-block__title {
    margin-bottom: 20px;
  }
}
.seo-block__title h2 {
  margin-bottom: 60px;
  text-align: center;
}
@media screen and (max-width: 690px) {
  .seo-block__title h2 {
    margin-bottom: 40px;
  }
}
.seo-block__title h3 {
  text-align: center;
}
.seo-block__content {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  gap: 20px;
  grid-auto-flow: dense;
  grid-auto-rows: auto;
}
@media screen and (max-width: 768px) {
  .seo-block__content {
    grid-template-columns: repeat(1, 1fr);
  }
}
@media screen and (max-width: 768px) {
  .seo-block__column {
    flex: 0 100%;
  }
}
.seo-block__column.hidden {
  display: none;
}
.seo-block__column li {
  text-align: justify;
}
.seo-block__column li::before {
  content: "";
  display: inline-block;
  width: 13px;
  height: 13px;
  margin-right: 10px;
  background-image: url('data:image/svg+xml;utf8,<svg xmlns="http://www.w3.org/2000/svg" viewBox="0 0 640 640"><path fill="%23000000" d="M0 352l64-64 160 160 352-352 64 64-416 416z"></path></svg>');
  background-size: contain;
}
.seo-block__text p:not(:last-child) {
  margin-bottom: 20px;
}
.seo-block__button {
  align-self: end;
  background: #fff;
}
.seo-block__button._rotate-button .icon {
  transform: rotate(180deg);
}
.seo-block__button::before {
  background: #2b3769;
}
@media screen and (max-width: 768px) {
  .seo-block__button {
    margin-top: 16px;
    justify-self: center;
    grid-column: 1/2 !important;
  }
}
.seo-block__button span {
  white-space: nowrap;
  font-weight: 400;
  line-height: 125%; /* 25px/20 */
  text-align: center;
}
.seo-block__button--even {
  grid-column: -3/-1;
  justify-self: center;
  margin-top: 20px;
}
.seo-block__icon-btn .icon-arrow-down::before {
  color: #fff;
}